import React from 'react';
import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import styled from 'styled-components';
import { Link } from 'react-router-dom';


function Results() {

    const [searchResults, setSearchResults] = useState([]);
    let params = useParams();

    const getResult = async (result) => {
        const data = await fetch(`https://api.spoonacular.com/recipes/complexSearch?apiKey=${process.env.REACT_APP_API_KEY}&number=9&query=${result}`)
        const recipes = await data.json();
        setSearchResults(recipes.results);
      }

      useEffect(() => {
        getResult(params.search)
      }, [params.search])

  return (
    <Grid>
        {searchResults.map((item) => {
            return (
                <Card className='c-hove' key={item.id}>
                  <Link to={`/recipe/${item.id}`}>
                    <img src={item.image} alt={item.title} />
                    <h4>{item.title}</h4>
                  </Link>
                </Card>
            )
        })}

    </Grid>
  )
}

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(3, minmax(12rem, 1fr));
  grid-gap: 6rem;
`;

const Card = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20rem;
  min-width: 12rem;

  img{
    border-radius: 2rem;
    width: 100%;
    width: minmax(25rem, 100%);
  }

  a{
    text-decoration: none;
  }

  h4{
    text-align: center;
    padding: 1rem;
    color: #7ECC66;
  }
`

export default Results