import React from 'react';
import styled from 'styled-components';
import { useState } from 'react';
import { FaSearch } from 'react-icons/fa';
import { useNavigate } from 'react-router-dom';

function Search() {

  const [input, setInput] = useState("");

  const submitHandler = (e) => {
    e.preventDefault();
    navigate(`/search/${input}`);
  }

  const navigate = useNavigate();

  return (
    <SForm className='search-form' onSubmit={submitHandler}>
      <div>
        <FaSearch />
        <input onChange={(e) => setInput(e.target.value)} type="text" placeholder='Search' value={input}></input>
      </div>
    </SForm>
  )
}

const SForm = styled.form`
  max-width: 1fr;
  min-width: 12rem;
  
  div{
    position: relative;
    width: 100%;
  }

  input{
    width: 100%;
    border: none;
    background: linear-gradient(35deg, #7ECC66, #98D685);
    font-size: 1.5rem;
    padding: 1rem 3rem;
    color: black;
    border-radius: 1rem;
    outline: none;
  }

  input::placeholder{
    color: rgba(253, 253, 253, 0.7);
  }

  svg{
    color: #F1F6EC;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(100%, -50%);
  }
`

export default Search