import React from "react";
import {FaHamburger, FaPizzaSlice} from "react-icons/fa";
import {GiNoodles, GiChopsticks} from "react-icons/gi";
import styled from "styled-components";
import { NavLink } from "react-router-dom";

function Categories() {
  return (
    <List className="category-ctn">
        <SLink className="category-btn" to={"/cusine/italian"}>
            <FaPizzaSlice />
            <span>Italian</span>
        </SLink>
        <SLink className="category-btn" to={"/cusine/american"}>
            <FaHamburger />
            <span>American</span>
        </SLink>
        <SLink className="category-btn" to={"/cusine/thai"}>
            <GiNoodles />
            <span>Thai</span>
        </SLink>
        <SLink className="category-btn" to={"/cusine/japanese"}>
            <GiChopsticks />
            <span>Japanese</span>
        </SLink>
    </List>
  )
}

const List = styled.div`
    display: flex;
    justify-content: space-around;
    margin: 2rem 0 4rem 0;

`

const SLink = styled(NavLink)`
    max-width: 7rem;
    max-height: 7rem;
    cursor: pointer;
    transform: sclae(0.8);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    color: #101913;
    background: linear-gradient(to bottom, #C1D7C8, #CBEBC2);
    font-weight: 400;
    padding: 0.4rem;
    box-shadow: -1px 2px 10px rgba(20, 20, 20, 0.8);

    &.active {
        color: #F4F5F6;
        background: linear-gradient(to bottom, #2C5C1E, #162E0F);
        box-shadow: -1px 3px 10px rgba(20, 20, 20, 0.8);
    }
`

export default Categories
