import React, { useState, useEffect } from 'react'
import styled from 'styled-components';
import { Link, useParams } from 'react-router-dom';

function Cusine() {

  const [cuisine, setCuisine] = useState([]);
  let params = useParams();

  const getCusine = async (name) => {
    const data = await fetch(`https://api.spoonacular.com/recipes/complexSearch?apiKey=${process.env.REACT_APP_API_KEY}&number=12&cuisine=${name}`)
    const recipes = await data.json();
    setCuisine(recipes.results);
    console.log(recipes);
  }

  useEffect(() => {
    getCusine(params.type);
  }, [params.type])

  return (
    <Grid>
      {cuisine.map((item) => {
        return (
          <Card className='c-hove' key={item.id}>
            <Link to={`/recipe/${item.id}`}>
              <img src={item.image} alt={item.title}/>
              <span>{item.title}</span>
            </Link>
          </Card>
        )
      })}
    </Grid>
  )
}

const Grid = styled.div`
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 4.5rem;
  padding: 0 1rem;

  @media (min-width: 320px) and (max-width: 720px){
    grid-template-columns: 1fr;
  }
`;

const Card = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10rem;
  min-width: 8rem;

  img{
    border-radius: 2rem;
    width: minmax(8rem, 10rem);
  }

  a{
    text-decoration: none;
    text-align: center;
  }

  span{
    text-align: center;
    padding: 1rem;
    color: #7ECC66;
    font-size: 1.8rem
  }

  @media (min-width: 320px) and (max-width: 720px){
    width: 8rem;
    display: flex;
    flex-direction: row;
    img{
      width: 12rem;
    }
    span{
      font-size: 1rem;
    }
  }
`

export default Cusine